/* Default styles for navbar */
*{
    padding: 0%;
    margin: 0%;
}
.navbar {
    display: flex;
    /* justify-content:right; */
    
    align-items: center;
    background-color: #333;
    color: #fff;
    padding: 10px;
    height: 40px;
  }
  .logo {
    display: flex;
    float: right; /* Align the content to the left */
  }
  
  /* Style for the image inside the logo div */
  .logo img {
    width: 50px; /* Adjust the width as needed */
    height: auto; /* Maintain the aspect ratio */
    /* margin-right: 10px; Add margin to the right of the logo (adjust as needed) */
    /* Add any other styling you want for the logo image */
  }

  
  .link {
    float: right;
    text-decoration: none;
    color: #fff;
    margin: 0 10px;
    margin-left:40px ;
    transition: color 0.3s ease-in-out;
    font-size: 18px;
    font-weight: bolder;
  }
  
  .link:hover {
    color: #ffd700; /* Change color on hover */
  }
  
  /* SearchBox styles */
  /* Assuming SearchBox is a self-contained component */
  /* You may need to adjust these styles based on the actual implementation */
  
  /* Responsive styles for smaller screens */
  @media screen and (max-width: 600px) {
    .navbar {
      flex-direction: column;
      text-align: center;
    }
  
    .link {
      margin: 5px 0;
    }
  
    /* Adjust SearchBox styles for smaller screens */
    .search-box {
      width: 100%;
      margin-top: 10px;
    }
  }
  /* Default styles for SearchBox */
.searchBox {
    margin-top: 5px;
    margin-left: 40px;
  }
  
  .search {
    position: relative;
  }
  
  .searchControl {
    width: 80%;
    padding: 13px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 16px;
    text-align: center;
    transition: border-color 0.3s ease-in-out;
  }
  
  .searchControl:focus {
    outline: none;
    border-color: #4caf50; /* Change border color on focus */
  }
  
  /* Style the submit button (if applicable) */
  .searchButton {
    background-color: #4caf50;
    color: #fff;
    border: none;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .searchButton:hover {
    background-color: #45a049; /* Darker color on hover */
  }
  
  /* Responsive styles for smaller screens */
  @media screen and (max-width: 600px) {
    .searchControl {
      font-size: 14px;
      padding: 10px;
    }
  
    .searchButton {
      padding: 8px 12px;
    }
  }
  body {
    margin: 0;
}

header {
    position: relative;
    height: 150px; /* Set the desired height */
    overflow: hidden;
}

.sideShow {
    display: flex;
    overflow: hidden;
    width: 100vw; /* 100% of viewport width */
}

.sideShow img {
    width: 100%;
    height: 200px; /* Set the desired height */
    object-fit: cover;
}

/* Optional: Add animation for a simple slideshow effect */
@keyframes slide {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}

.sideShow img {
    animation: slide 5s linear infinite; /* Adjust duration as needed */
}



  
/* Default styles for container and card */
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 20px;
}

.rowMain {
  display: flex;
  flex-wrap: wrap;
}

.coId {
  margin: 10px;
}

.card {
  width: 100%;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  border-radius: 8px;
  overflow: hidden;
  margin-left: 15px;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.cardImg {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 2px solid #eee;
}

.card-body {
  padding: 15px;
}

.cardName {
  font-size: 1.5rem;
  margin-bottom: 8px;
  color: #333;
}

.cardA {
  color: #777;
  font-size: 1.2rem;
}

.cardP {
  margin-top: 12px;
  color: #555;
  line-height: 1.4;
}

.bt {
  display: inline-block;
  padding: 10px 16px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 8px;
  transition: background-color 0.3s;
  cursor: pointer;
}

.bt:hover {
  background-color: #0056b3;
}

/* Responsive styles for smaller screens */
@media screen and (max-width: 768px) {
  .card {
    width: calc(50% - 20px);
    margin-left: 0%;
  }

  .cardImg {
    height: 180px;
  }

  .card-body {
    padding: 12px;
  }

  .cardName {
    font-size: 1.3rem;
  }

  .cardA {
    font-size: 1.1rem;
  }
}

  /* Footer.css */

footer {
  background: linear-gradient(135deg, #1a1a1a, #333);
  color: #fff;
  padding: 30px 20px;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.footer-section {
  flex: 1;
  margin: 10px;
}

.footer-section h3 {
  font-size: 1.8rem;
  margin-bottom: 15px;
  color: #ffd700; /* Golden yellow */
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-section li {
  margin-bottom: 8px;
}

.footer-section a {
  color: #bbb;
  text-decoration: none;
  display: block;
  transition: color 0.3s;
  font-size: 1.1rem;
}

.footer-section a:hover {
  color: #ffd700;
}

.footer-section p {
  margin: 0;
  color: #ccc;
}

.social-icons {
  display: flex;
}

.social-icons a {
  color: #bbb;
  text-decoration: none;
  margin-right: 15px;
  font-size: 1.8rem;
  transition: color 0.3s;
}

.social-icons i {
  transition: color 0.3s;
}

.social-icons a:hover i {
  color: #ffd700;
}

.footer-bottom {
  margin-top: 20px;
  text-align: center;
  color: #ccc;
}

.footer-bottom p {
  margin: 0;
  font-size: 1rem;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .footer-section {
    flex: none;
    width: 100%;
  }

  .footer-section h3 {
    font-size: 1.5rem;
  }

  .footer-section a {
    font-size: 1rem;
  }

  .social-icons a {
    font-size: 1.5rem;
  }
}
