/* Add these styles to your existing CSS */

.containerMain {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh; /* Adjust the height based on your design */
  }
  
  .btnInfo {
    margin-bottom: 20px;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .btnInfo:hover {
    background-color: #0056b3;
  }
  
  .cardI {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 100%; /* Adjust the width based on your design */
    max-width: 400px; /* Set a maximum width */
    margin-bottom: 20px;
  }
  
  .cardImg {
    width: 100%;
    height: auto;
  }
  
  .name {
    background-color: #c7b5b5;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    width: 100%; /* Adjust the width based on your design */
    max-width: 600px; /* Set a maximum width */
  }
  
  .name h2,
  .name p {
    padding: 15px;
    margin: 0;
  }
  
  .name h2 {
    color: #333;
    font-size: 1.8rem;
  }
  
  .pTage {
    color: #777;
    font-size: 1.2rem;
    margin-bottom: 15px;
  }
  
  /* Responsive styles */
  @media screen and (max-width: 768px) {
    .cardI, .name {
      width: 90%;
    }
  
    .name h2 {
      font-size: 1.5rem;
    }
  
    .pTage,
    .name p {
      font-size: 1rem;
    }
  }
  /* About.css */

.about-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 30px;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
  }
  
  .about-title {
    color: #2c3e50;
    font-size: 3rem;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .about-paragraph {
    color: #555;
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  /* Additional Styling */
  .about-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, #3498db, #e74c3c);
    background-size: 200% 200%;
    animation: gradientAnimation 5s infinite;
    border-radius: 15px;
    z-index: -1;
  }
  
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  /* Responsive styles */
  @media screen and (max-width: 768px) {
    .about-title {
      font-size: 2.5rem;
    }
  
    .about-paragraph {
      font-size: 1rem;
    }
  }
/* Contact.css */

.contact-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 30px;
    background-color: #f9f9f9;
    border-radius: 15px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: hidden;
  }
  
  .contact-title {
    color: #3498db;
    font-size: 3rem;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .contact-text,
  .contact-email,
  .contact-info p {
    color: #555;
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  .contact-email {
    color: #e74c3c;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
  }
  
  .contact-label {
    color: #333;
    font-size: 1.2rem;
    margin-bottom: 5px;
  }
  
  .contact-input,
  .contact-textarea {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    width: 100%;
  }
  
  .contact-button {
    background-color: #3498db;
    color: #fff;
    padding: 15px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .contact-button:hover {
    background-color: #2980b9;
  }
  
  .contact-info {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin-top: 30px;
  }
  
  .contact-info h2 {
    color: #3498db;
    font-size: 2rem;
    margin-bottom: 15px;
  }
  
  .contact-info p {
    color: #555;
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  .contact-container a{
    text-decoration: none;
    color: #3498db;
  }
  /* Responsive styles */
  @media screen and (max-width: 768px) {
    .contact-title {
      font-size: 2.5rem;
    }
  
    .contact-text,
    .contact-email,
    .contact-info p {
      font-size: 1rem;
    }
  
    .contact-input,
    .contact-textarea,
    .contact-button {
      font-size: 1rem;
    }
  
    .contact-info {
      padding: 15px;
    }
  
    .contact-info h2 {
      font-size: 1.8rem;
    }
  }
    



